import style from "../../style/header.module.sass";
import drawerAnim from "../../style/headerDrawer.module.sass"

import EyeOfJudgement from "../../res/svg/eyeOfJudgementEllipse.svg"
import { useState, useEffect } from "react";

import { OutsideAlerter } from "../../hooks/clickOutside";

import { Dropdown, DropdownItem } from '../MultilevelMenu';
import { CSSTransition, TransitionGroup, SwitchTransition } from "react-transition-group";
import { EyePlaceholder } from "./EyeOfJudgement";

import Link from 'next/link'
import { useIdentity } from "../../hooks/identity";

export function Header(props) {
    //Drawer
    var [drawerButton, setDrawerButton] = useState(-2);
    var [drawer, setDrawer] = useState(undefined);

    //collect refs
    var items = props.children || [];
    var refs = [];

    if (!Array.isArray(items)) {
        items = [items];
    }
    var itemObj = items.map((item, index) => {
        return React.cloneElement(item, { ref: (ref) => refs.push(ref), key: index, addDrawer: addDrawer(index), removeDrawer: removeDrawer(index), currentDrawer: drawer, rerender: props.rerenderFunc })
    });

    function addDrawer(nr) {
        return (drawer) => {
            setDrawer(drawer);
            setDrawerButton(nr);
        }
    }

    function removeDrawer(nr) {
        return () => {
            setDrawer(undefined);
            setDrawerButton(-2);
        }
    }
    var open = drawerButton > -1;

    function noop() {}

    return (
        <>
            <nav className={style.header}>
                <div className={style.headerStart}>
                    <Link href="/"><EyePlaceholder size={40} id="default" onClick={noop}></EyePlaceholder></Link>
                    <div className={style.title}>
                        {props.title}
                    </div>
                </div>
                <ul className={style.headerTray}>
                    {itemObj}
                </ul>
            </nav>
            <div style={{ paddingTop: "50px" }}></div>

            <div className={style.headerDrawerWrap}>
                <SwitchTransition mode={"out-in"}>
                    <CSSTransition
                        classNames={{ ...drawerAnim }}
                        className={style.headerDrawer}
                        timeout={open ? 500 : 0}
                        key={drawerButton}
                    >
                        <div>{drawer}</div>
                    </CSSTransition>
                </SwitchTransition>
            </div>
        </>
    );
}


export const HeaderButton = React.forwardRef(function HeaderButton(props, ref) {
    const [open, setOpen] = useState(false);
    const [drawerOpen, setDrawerOpen] = useState(false);

    //dropdown will only open if component IS a dropdown
    var openD = typeof props.children === "object" && props.children.type.name === Dropdown.name;

    function clickOutside() {
        setOpen(false);
    }
    useEffect(() => {
        if (!props.currentDrawer) { setDrawerOpen(false);}
    }, [props.currentDrawer])
    
    var drawer;
    if(props.drawer && !Array.isArray(props.drawer)) {
        drawer = React.cloneElement(props.drawer, {close: props.removeDrawer, rerender: props.rerender});
    }

    return (
        <OutsideAlerter className={style.headerItem} component="li" onClickOutside={clickOutside}>
            <div className={style.headerButton} data-drawer={drawerOpen} onClick={() => {
                setOpen(!open);
                if (!drawerOpen) {
                    if (props.drawer) {
                        props.addDrawer(drawer);
                        setDrawerOpen(true)
                    } else {
                        props.removeDrawer()
                    };
                } else {
                    props.removeDrawer();
                }
            }} ref={ref}>
                {props.imgUrl && <img src ={props.imgUrl} alt="image icon"/>}
                {props.icon && props.icon}
            </div>

            {open ?
                <div>{props.children}</div>
                : undefined}
        </OutsideAlerter>
    );
});

export const UserHeaderButton = React.forwardRef(function UserHeaderButton(props, ref) {
    const [id, axios] = useIdentity()

    return (<HeaderButton ref={ref} imgUrl={id.active ? id.users[id.active].avatarUrl : undefined} {...props}>{props.children}</HeaderButton> )
});